import marked from 'marked'
import { useState } from 'react';
import styled from 'styled-components'

const DivPrincipal = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 10px 20px;
`

const TextAreaP = styled.textarea`
  width: 80%;
  height: 200px;
`

const DivPreview = styled.div`
  background: rgb(0 126 255 / 50%);
  width: 100%;
  margin: 10px;
  padding: 30px;
`

const HrSeparator = styled.hr`
  width: 100%;
  margin-top: 20px;
`

const initialState = `# Welcome to my React Markdown Previewer!

  ## This is a sub-heading...
  ### And here's some other cool stuff:

  Heres some code, \`<div></div>\`, between 2 backticks.

  \`\`\`
  // this is multi-line code:

  function anotherExample(firstLine, lastLine) {
    if (firstLine == '\`\`\`' && lastLine == '\`\`\`') {
      return multiLineCode;
    }
  }
  \`\`\`

  You can also make text *bold*... whoa!
  Or italic.
  Or... wait for it... *both!*
  And feel free to go crazy ~crossing stuff out~.

  There's also [links](https://www.freecodecamp.com), and
  > Block Quotes!

  And if you want to get really crazy, even tables:

  Wild Header | Crazy Header | Another Header?
  ------------ | ------------- | -------------
  Your content can | be here, and it | can be here....
  And here. | Okay. | I think we get it.

  - And of course there are lists.
    - Some are bulleted.
      - With different indentation levels.
          - That look like this.


  1. And there are numbererd lists too.
  1. Use just 1s if you want!
  1. And last but not least, let's not forget embedded images:

  ![React Logo w/ Text](https://i.ibb.co/HhjxxPm/logo2.png) Sajoma Pets
  `

function App() {

  const [text, setText] = useState(initialState)

  const markdown = marked(text, {breaks: true})

  const handleEditar = e => {
    setText(e.target.value)
  }

  return (
    <DivPrincipal className="App">
      <h2>Edit</h2>
      <TextAreaP id='editor' onChange={handleEditar} name='text' value={text} />
      <HrSeparator />
      <h2>Preview</h2>
      <DivPreview id='preview' dangerouslySetInnerHTML={{__html: markdown}} />
    </DivPrincipal>
  );
}

export default App;
